import React from 'react'
import style from './Menu.module.css'

const Menu = (props) => {
  return (
    <div onClick={props.hideSidebar} className={style.item}>
      <img className={style.icon} src="https://www.flaticon.com/svg/static/icons/svg/966/966313.svg" alt=""/>
    </div>
  )
}

export default Menu

